import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Device } from '@ionic-native/device/ngx';
import { Platform, MenuController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { AnalyticsService } from 'src/services/analytics.service';
import { CommonService } from 'src/services/common.service';
import { EventProviderService } from 'src/services/event-provider.service';
import { LoaderService } from 'src/services/loader.service';
import { SermonService } from 'src/services/sermon.service';
import { ToastService } from 'src/services/toast.service';
import { Storage } from '@ionic/storage';
import { Location } from '@angular/common';
import { ApiService } from 'src/services/api.service';

@Component({
  selector: 'app-station',
  templateUrl: './station.page.html',
  styleUrls: ['./station.page.scss'],
})
export class StationPage implements OnInit {

  theme: any;
  ch_id: any;
  station_page: any = {}
  stations: any[] = [];
  userId: any = '';
  pageno = 1;
  page_id:any;
  // allSermons: any;
  // latestSermon: any;
  // popularSermons: any;
  // recentSermons: any;
  // otherSermons: any;
  // showOtherSermons: boolean = false;
  // latestspeaker: any;
  // latesttitle: any;
  // latestimage: any;
  // showPopularSermons: any = false;
  // sermons_url: any;
  // search_sermon: any;
  // enable_home: any;
  // start_time_analytics: any;
  // custom_name: any;
  // ch_id: any;
  // user: any;
  // theme: any;
  // show_popular_sermons: any;
  // apppage_id: any;
  // link_type: any;
  // alertMessage: any;
  // page_in_home_tabs: any = false;

  constructor(
    private storage: Storage,
    private loader: LoaderService,
    private sermonsService: SermonService,
    private router: Router,
    private location: Location,
    private toast: ToastService,
    private common: CommonService,
    private event_provider: EventProviderService,
    private platform: Platform,
    private device: Device,
    private analytics: AnalyticsService,
    private translate: TranslateService,
    private menu: MenuController,
    private route: ActivatedRoute,
    private apiService: ApiService
  ) {
    this.storage.get('user').then(res => {
      console.log(res);
      if (res) {
        this.userId = res.user.id;        
      }
    });
    this.loader.presentLoading().then(() => {});
    // getting theme from local storage
    this.page_id = this.route.snapshot.queryParams.page_id;
    this.storage.get('theme').then(res => {

      // getting app_pages
      const body = JSON.stringify({ id: res.ch_id });
      this.common.getTheme(body).subscribe(async (res: any) => {
        console.log(res);
        this.loader.stopLoading();
        this.theme = res.church;
        this.ch_id = res.church.ch_id;
        let page = res.apppages.filter(x => x.page_type === 'page' && x.id === this.route.snapshot.queryParams.page_id);
        if (page.length > 0) {
          this.station_page = page[0];
          this.getStations();
          //   this.apppage_id = sermon_page[0].id;
          //   this.sermons_url = sermon_page[0].url;
          //   this.page_name = sermon_page[0].title;
          //   this.custom_name = sermon_page[0].name;
          //   this.show_popular_sermons = sermon_page[0].show_popular_sermons;

          //   // getting sermons from URL
          //   if (this.sermons_url.includes('.rss') || this.sermons_url.includes('-rss')) {
          //     // if url is .rss feed
          //     // this.getRss(this.apppage_id, this.ch_id)
          //   } else {
          //     // this.getSermonsFromWordpress(this.sermons_url);
          //   }
        }
      });
    });
  }

  // navigating to back page
  gotobackPage() {
    if (this.ch_id === '1000') {
      this.menu.toggle();
    } else {
      this.location.back();
    }
  }
  ionViewWillEnter() {
    this.ngOnInit();
  
    this.event_provider.getChurchData.subscribe(async (res: any) => {
        if (!(Object.keys(res).length === 0)) {
            // asking for password
            var id = this.route.snapshot.queryParams.page_id
            let app_pages = res.apppages.find(x => x.id === id);
            if (app_pages.password_protection_toggle === 'true') {
                this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
            }
        }
    });
  
  }

  ionViewDidEnter() {
    this.pageno = 1;
    this.event_provider.hidefooter(true, 'other');
  }
  ngOnInit() {
  }

  getStations(ev?: any) {
    if (ev) {
      this.pageno = this.pageno + 1;
      this.apiService.getStations(this.pageno, this.ch_id).subscribe(
        (res: any) => {
          console.log(res);
          ev.target.complete();
          if (res.status) {
            this.stations.push(...res.data);
          }
        },
        (err: any) => {
          console.log(err);
        }
      );
    } else {
      this.apiService.getStations(this.pageno, this.ch_id).subscribe(
        (res: any) => {
          console.log(res);
          if (res.status) {
            this.stations = res.data;
          }
        },
        (err: any) => {
          console.log(err);
        }
      );
    }
    
  }

  playAudio(url) {
    this.event_provider.setAudioUrl({ url: url, autoPlay: true, dateTime:Date.now() });
  }

  makeFavourite(church_id) {
    this.apiService.makeStationFavourite(this.userId, church_id).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status) {
          this.getStations();
        }
      },
      (err: any) => {
        console.log(err);
      }
    );
  }


  hasAudioLink(link: string) {
    if (this.ch_id == '1124') {
        if (/.aif|.cda|.mid|.midi|.mp3|.mpa|.ogg|.wav|.wma|.wpl/.test(link)) {
            return true;
        }
        return false;
    } else {
        return true;
    }
   
}

}
